@charset "UTF-8";
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 100%;
}

body {
  font-size: 0.9rem;
  font-family: sans-serif;
  color: #444444;
}

img {
  max-width: 100%;
}

ul {
  list-style: none;
}

.container {
  max-width: 1000px;
  margin: 0 auto;
}

.inner {
  max-width: 800px;
  margin: 0 auto;
}
@media (max-width: 1024px) {
  .inner {
    padding: 0 20px;
  }
}

.title {
  font-size: 1.125rem;
  font-weight: bold;
  margin-bottom: 30px;
  text-align: center;
}

/*-------------------------------------------
header
-------------------------------------------*/
#header {
  margin-top: 60px;
}
@media (max-width: 1024px) {
  #header {
    padding: 0 10px;
  }
}
#header .logo {
  width: 165px;
  line-height: 1px;
  margin-bottom: 15px;
}
#header .logo a {
  display: block;
}

/*-------------------------------------------
Mainvisual
-------------------------------------------*/
#mainvisual {
  margin-bottom: 80px;
}
#mainvisual img {
  object-fit: cover;
}

/*-------------------------------------------
INDEX
-------------------------------------------*/
#index {
  background: #f3f3f3;
  padding: 60px;
  margin-bottom: 60px;
}
@media (max-width: 1024px) {
  #index {
    padding: 40px 20px;
  }
}
#index .content {
  border: 1px solid #444444;
  padding: 30px;
}
#index .content ol {
  display: table;
  margin: 0 auto;
}
#index .content ol li {
  margin-bottom: 20px;
}
#index .content ol li:last-child {
  margin-bottom: 0;
}

/*-------------------------------------------
items
-------------------------------------------*/
.items {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin: 30px 0 45px 0;
}
@media (max-width: 1024px) {
  .items {
    flex-direction: column;
  }
}
.items .item {
  width: 49%;
  margin-bottom: 15px;
}
@media (max-width: 1024px) {
  .items .item {
    width: 100%;
    text-align: center;
  }
}
.items .item img {
  /*
  画像の下にできる隙間を消す
  */
  vertical-align: bottom;
}

/*-------------------------------------------
DETAL
-------------------------------------------*/
#detail {
  background: #f3f3f3;
  padding: 60px;
  margin-bottom: 60px;
}
@media (max-width: 1024px) {
  #detail {
    padding: 40px 20px;
  }
}
#detail .flex {
  display: flex;
}
@media (max-width: 1024px) {
  #detail .flex {
    flex-direction: column;
  }
}
#detail .flex dl {
  width: 35%;
  border-right: 1px solid #888888;
}
@media (max-width: 1024px) {
  #detail .flex dl {
    width: 100%;
    border-right: none;
    border-bottom: 1px solid #888888;
    padding: 0 0 40px 0;
  }
}
#detail .flex dl dt {
  font-weight: 700;
}
#detail .flex dl dd {
  margin-bottom: 10px;
}
#detail .flex dl dd:last-child {
  margin-bottom: 0;
}
#detail .flex .text {
  width: 65%;
  padding-left: 40px;
}
@media (max-width: 1024px) {
  #detail .flex .text {
    width: 100%;
    padding: 40px 0 0 0;
  }
}
#detail .flex .text p {
  margin-bottom: 20px;
}
#detail .flex .text .link {
  color: #888888;
}
#detail .flex .text .link:hover {
  opacity: 0.8;
}

/*-------------------------------------------
Footer
-------------------------------------------*/
#footer {
  text-align: center;
  padding: 15px 0;
  font-size: 0.625rem;
}